import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"
import styled from "styled-components"
import Layout from "../components/layout"
import SEO from "../components/seo"

const SectionWrapper = styled.section`
  margin: 0;
  padding: 2rem 0 1rem 0;
  text-align: center;

  & p {
    margin: 0;
    padding: 0 0 1rem 0;
  }
`
const SectionTitle = styled.h2`
  margin: 0;
  padding: 1rem 0;
`

const SectionContents = styled.div`
  margin: 0;
  padding: 0 0 1rem 0;
`

const SocialLink = styled.a`
  display: inline-block;
  margin: 0;
  background-color: #202c6a;
  font-size: 0.6rem;
  padding: 0.1rem 0.4rem;
  border-radius: 0.1rem;
  color: white;
  &:hover {
    background-color: #ffcc06;
    text-decoration: none;
  }
`

const AboutPage = () => {
  const data = useStaticQuery(graphql`
    query {
      profileImage: file(relativePath: { eq: "haru-icon.png" }) {
        childImageSharp {
          fixed(width: 150, height: 150) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)
  return (
    <Layout>
      <SEO title="About" />

      <SectionWrapper>
        <Img
          fixed={data.profileImage.childImageSharp.fixed}
          alt="haru-thumbs-up"
        />
        <SectionTitle>Haru</SectionTitle>
        <SectionContents>
          <p>
            Software Engineer
            <br />
            (Currently iOS App Developer)
          </p>
          <p>
            <SocialLink href="https://github.com/soharu">
              Github: @soharu
            </SocialLink>
          </p>
        </SectionContents>
      </SectionWrapper>
    </Layout>
  )
}

export default AboutPage
